import { Sprite, Texture } from "pixi.js";
import { sendEvent } from "../..";
import { gameSettings, stateManager } from "../../core/BaseGame";
import { BaseState } from "../../core/states/BaseState";
import { Button } from "../../core/ui/Button";
import { MenuState } from "./MenuState";

export class ComicsState extends BaseState {
    private comics: Sprite;
    private step: number;
    private nextBtn: Button;
    private prevBtn: Button;
    private okBtn: Button;
    public create(): void {
        this.comics = Sprite.from(Texture.from("comic_00.png"));
        this.comics.anchor.set(0.5, 0.5);
        this.addChild(this.comics);
        
        
        this.nextBtn = new Button("next_button.png", "next_button.png");
        this.nextBtn.position.set(160,gameSettings.originalHeight/2 - this.nextBtn.height/2);
        this.addChild(this.nextBtn);
        this.nextBtn.on("pointerup", () => {
            sendEvent("click", "comics", "next");
            this.onNextClicked();
        });
        this.prevBtn = new Button("back_button.png", "back_button.png");
        this.prevBtn.position.set(-160,this.nextBtn.y);
        this.addChild(this.prevBtn);
        this.prevBtn.on("pointerup", () => {
            sendEvent("click", "comics", "prev");
            this.onPrevClicked();
        });

        this.okBtn = new Button("ok_button.png", "ok_button.png");
        this.okBtn.on("pointerup", ()=> {
            stateManager.start(MenuState);
        });
        this.okBtn.position.y = this.nextBtn.y - 50;
        this.addChild(this.okBtn);

        this.prevBtn.visible = false;
        this.nextBtn.visible = false;
        this.okBtn.visible = false;
        
    }
    public start(...params: any): void {
        sendEvent("start", "menu", "level");
        this.step = 0;
        this.prevBtn.visible = false;
        this.nextBtn.visible = true;
        this.okBtn.visible = false;
    }
    public async close() {
        sendEvent("close", "menu", "level");
        for (let i = 0; i < 6; i++) {
            Texture.removeFromCache(`comic_0${i}.png`);
        }
        this.removeChild(this.okBtn);
        this.okBtn = undefined;
        this.destroy({children: true, texture: true});
    }
    private onNextClicked() {
        this.step++;
        if (this.step < 5) {
            this.updateImage();
            this.prevBtn.visible = true;
            this.nextBtn.visible = true;
        } else if (this.step === 5) {
            this.updateImage();
            this.okBtn.visible = true;
            this.prevBtn.visible = false;
            this.nextBtn.visible = false;
        }
    }
    private onPrevClicked() {
        this.step--;
        this.prevBtn.visible = this.step > 0;
        this.updateImage();
    }
    private updateImage() {
        this.comics.texture = Texture.from(`comic_0${this.step}.png`);
    }
}