import gsap, { Bounce } from "gsap";
import { Emitter } from "pixi-particles";
import { Container, NineSlicePlane, Sprite, Text, Texture, Ticker } from "pixi.js";
import { gameSettings } from "../../../core/BaseGame";
import { getStorageData, setStorageData } from "../../../core/natives/SocialManager";
import { Button } from "../../../core/ui/Button";
import { EIAPBonus } from "../GameViewState";
import { WheelBox } from "./levelstart/WheelBox";
import { PowerupButton } from "./PowerupButton";

export class LevelFinished extends Container {
    private back: Sprite;
    private popup: Container;
    private callback: () => void;
    private scoreTxt: Text;
    private title: Text;
    private stars: Sprite[];
    private emitter: Emitter;
    private prizeIcon: Sprite;
    constructor() {
        super();
        this.back = Sprite.from(Texture.WHITE);
        this.back.anchor.set(0.5, 0.5);
        this.back.width = gameSettings.originalWidth;
        this.back.height = gameSettings.originalHeight;
        this.back.tint = 0x000000;
        this.back.alpha = 0;
        this.addChild(this.back);

        const particles = new Container();
        this.addChild(particles);

        this.popup = new Container();
        this.popup.scale.set(0,0);
        this.addChild(this.popup);

        const popupBack = new NineSlicePlane(Texture.from("top_back.png"), 46, 50, 46, 50);
        popupBack.pivot.set(300, 400);
        popupBack.width = 600;
        popupBack.height = 800;
        this.popup.addChild(popupBack);

        this.title = new Text("Well done!", {fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 70, fill: 0x5f40e3, align: "center", fontWeight: "bold"})
        this.title.anchor.set(0.5, 0.5);
        this.title.position.y = -340;
        this.popup.addChild(this.title);

        this.scoreTxt = new Text("0", {fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 120, fill: 0x000000, align: "center", letterSpacing: 7, fontWeight: "bold"})
        this.scoreTxt.anchor.set(0.5, 0.5);
        this.scoreTxt.y = 40;
        this.popup.addChild(this.scoreTxt);

        this.stars = [];
        let xx = -120;
        for (let i = 0; i < 3; i++) {
            const star = Sprite.from(Texture.from("like.png"));
            star.anchor.set(0.5, 0.5);
            star.position.set(xx, -150);
            this.popup.addChild(star);
            this.stars.push(star);
            star.visible = false;
            xx += 120;
        }
        this.popup.addChild(this.stars[1]);

        this.prizeIcon = new Sprite();
        this.prizeIcon.anchor.set(0.5, 0.5);
        this.prizeIcon.scale.set(0.5, 0.5);
        this.prizeIcon.position.copyFrom(this.stars[2]);
        this.prizeIcon.x += 50;
        this.prizeIcon.y -= 50;
        this.popup.addChild(this.prizeIcon);
        this.prizeIcon.visible = false;

        const okBut = new Button("ok_button.png", "ok_button.png");
        okBut.on("pointerup", ()=> {
            this.hide();
        });
        okBut.position.y = 300;
        this.popup.addChild(okBut);

        this.emitter = new Emitter(particles, "like.png", gameSettings.particles);
    }
    public async show(won: boolean, score: number, conditions: number[], callback: ()=>void, level: number, prizes: EIAPBonus[] | undefined) {
        console.log(level.toString(), prizes);
        this.title.text = (won) ? "Well done!" : "So sorry...";
        const prize = prizes ? prizes[0] : undefined;
        if (prize !== undefined && score >= conditions[2]) {
            const powerupDone = await getStorageData("powerupDone" + level.toString(), "0");
            console.log("pd",powerupDone);
            if (powerupDone === "0") {
                this.prizeIcon.visible = true;
                this.prizeIcon.scale.set(0, 0);
                this.prizeIcon.texture = WheelBox.getTexture(prize);


                const storageData = await getStorageData(PowerupButton.getStorageId(prize), "0");
                const amount = parseInt(storageData);
                await setStorageData(PowerupButton.getStorageId(prize), (amount+1).toString());
                await setStorageData("powerupDone" + level.toString(), "1");
            }
        }
        for (let i = 0; i < this.stars.length; i++) {
            const star = this.stars[i];
            star.visible = true;
            star.scale.set(0,0);
            (star as any).showed = false;
        }
        this.callback = callback;
        this.visible = true;
        this.back.alpha = 0;
        this.popup.scale.set(0,0);
        //const stars = calculateStars(score, conditions);
        const o = {s: 0};
        this.scoreTxt.text = "0";
        gsap.to(o, {s: score, delay: 1, duration: 2, onUpdate: ()=> {
            this.scoreTxt.text = Math.round(o.s).toString();
            for (let i = 0; i < conditions.length; i++) {
                const star = this.stars[i];
                //console.log(i, star)
                if (o.s >= conditions[i] && !(star as any).showed) {
                    (star as any).showed = true;
                    const s = (i === 1) ? 1.5 : 1;
                    gsap.to(star.scale, {x: s, y: s, duration: 0.2, ease: Bounce.easeOut});
                    if (this.prizeIcon.visible && i === 2) {
                        gsap.to(this.prizeIcon.scale, {x: 0.5, y: 0.5, duration: 0.2, ease: Bounce.easeOut});
                    }
                }
            }
        }});
        gsap.to(this.back, {alpha: 0.5, duration: 0.5});
        gsap.to(this.popup.scale, {x: 1, y: 1, duration: 1, ease: Bounce.easeOut});
        if (won) {
            gsap.delayedCall(1, ()=> {
                this.emitter.emit = true;
                Ticker.shared.add(this.onTickEvent);
            });
        }
    }
    private readonly onTickEvent = (deltaTime: number) => {
        this.emitter.update(deltaTime / 70);
    };
    private hide() {
        gsap.to(this.back, {alpha: 0, duration: 0.2});
        gsap.to(this.popup.scale, {x: 0, y: 0, duration: 0.2, onComplete: ()=> {
            this.emitter.emit = false;
            this.emitter.cleanup();
            Ticker.shared.remove(this.onTickEvent);
            this.prizeIcon.visible = false;
            this.visible = false;
            this.callback();
        }});
    }
}