import gsap from "gsap";
import { Container, Sprite, Texture } from "pixi.js";

export class NextBlocksTimer extends Container {
    private bar: Sprite;
    private barMask: Sprite;
    private initialWidth: number = 596;
    private initialHeight: number = 30;
    public isFrozen: boolean;
    private delay: gsap.core.Tween;
    private frozen: Sprite;
    private frozenBack: Sprite;
    private back: Sprite;
    barBorder: Sprite;
    constructor() {
        super();
        this.back = Sprite.from(Texture.from("timer_1.png"));
        this.addChild(this.back);
        this.frozenBack = Sprite.from(Texture.from("timer_frozen_1.png"));
        this.frozenBack.visible = false;
        this.addChild(this.frozenBack);

        this.bar = Sprite.from(Texture.from("timer_2.png"));
        this.addChild(this.bar);

        this.barMask = new Sprite(Texture.WHITE);
        this.barMask.x = 16;
        this.barMask.width = 0;
        this.barMask.height = this.bar.height;
        this.addChild(this.barMask);
        this.bar.mask = this.barMask;

        this.barBorder = Sprite.from(Texture.from("timer_0.png"));
        this.barBorder.anchor.set(0.5, 0.5);
        this.barBorder.x = 100;
        this.barBorder.y = this.bar.height * 0.5;
        this.addChild(this.barBorder);
        this.barBorder.visible = false;

        this.frozen = new Sprite(Texture.from("timer_frozen_0.png"));
        this.frozen.visible = false;
        this.addChild(this.frozen);
    }
    public setProgress(p: number) {
        this.barMask.width = this.initialWidth * p;
        this.barBorder.visible = p > 0.01 && p < 0.99;
        this.barBorder.x = this.barMask.x + this.initialWidth * p;
    }
    public unFreeze() {
        this.isFrozen = false;
        gsap.killTweensOf(this.frozen);
        gsap.to(this.frozenBack, {alpha: 0, duration: 0.3});
        gsap.to(this.back, {alpha: 1, duration: 0.3});
        gsap.to(this.frozen, {alpha: 0, duration: 0.3, onComplete: ()=> {
            this.frozen.visible = false;
            this.frozenBack.visible = false;
        }});
    }
    public freeze(seconds: number = -1) {
        this.isFrozen = true;
        this.frozen.visible = true;
        this.frozen.alpha = 0;
        this.frozenBack.visible = true;
        this.frozenBack.alpha = 0;
        gsap.killTweensOf(this.frozen);
        gsap.killTweensOf(this.frozenBack);
        gsap.killTweensOf(this.back);
        gsap.to(this.frozen, {alpha: 1, duration: 0.3});
        gsap.to(this.frozenBack, {alpha: 1, duration: 0.3});
        gsap.to(this.back, {alpha: 0, duration: 0.3});
        if (this.delay) {
            this.delay.kill();
        }
        if (seconds === -1) return;
        this.delay = gsap.delayedCall(seconds, ()=> {
            this.unFreeze();
        });
    }
    public clear() {
        if (this.delay) {
            this.delay.kill();
            this.delay = undefined;
        }
        gsap.killTweensOf(this.frozen);
        this.frozen.visible = false;
        this.frozenBack.visible = false;
        this.back.alpha = 1;
        this.isFrozen = false;
    }
}