import { Sprite, Text, Texture } from "pixi.js";
import { gameSettings } from "../../../core/BaseGame";
import { AdType, getStorageData, setStorageData, showADS } from "../../../core/natives/SocialManager";
import { Button } from "../../../core/ui/Button";
import { EIAPBonus } from "../GameViewState";

export class PowerupButton extends Button {
    private _id: EIAPBonus;
    private icon: Sprite;
    private txt: Text;
    private amount: number;

    constructor() {
        super("", "");
        this.amount = 1;

        this.txt = new Text(this.amount.toString(), {fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 50, fill: 0x000000, align: "center"});
        this.txt.anchor.set(0.5, 0.5);
        this.txt.x = 90;
        this.txt.y = 90;
        this.addChild(this.txt);


        this.icon = Sprite.from(Texture.from("bonus_ads.png"));
        this.icon.x = 15;
        this.icon.y = 35;
        this.addChild(this.icon);
        this.icon.visible = false;

        this.on("pointerup", () => {
            this.onPressed();
        });
    }
    public get id() {
        return this._id;
    }
    public async setData (id: EIAPBonus, normalState: Texture, downState: Texture) {
        this._id = id;
        this.normalTexture = normalState;
        this.downTexture = downState;
        this.btn.texture = this.normalTexture;

        this.icon.visible = false;
        let storageData = await getStorageData(PowerupButton.getStorageId(this.id), undefined);
        if (storageData === undefined) {
            storageData = "1";
            await setStorageData(PowerupButton.getStorageId(this.id), storageData);
        }
        this.amount = parseInt(storageData);
        this.txt.text = storageData;
        this.updateTxt();

        this.scale.set(0.6, 0.6);
    }
    public static getStorageId(id: EIAPBonus) {
        return "powerup_" + id.toString();
    }
    private updateTxt() {
        if (this.amount === 0) {
            this.btn.alpha = 0.6;
            this.icon.visible = true;
            this.txt.visible = false;
        } else {
            this.btn.alpha = 1;
            this.icon.visible = false;
            this.txt.visible = true;
        }
    }
    private async onPressed() {
        if (this.amount > 0) {
            this.amount--;
            await setStorageData(PowerupButton.getStorageId(this.id), this.amount.toString());
            this.updateTxt();
            this.emit("activate", this.id);
        } else {
            gameSettings.paused = true;
            const success = await showADS(AdType.RewardedVideo);
            gameSettings.paused = false;
            if (success) {
                this.emit("activate", this.id);
            }
            
        }
    }
}