import { Container, NineSlicePlane, Texture, Text, Sprite } from "pixi.js";
import { ILevelData } from "../GameViewState";

export class MapItem extends Container {
    private txt: Text;
    private lockedTxt: Text;
    private hearts: Sprite[];
    constructor() {
        super();
        this.txt = new Text("Level 1",{fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 50, fill: 0x000000, fontWeight: "bold", align: "center"});
        this.txt.anchor.set(0.5, 1);
        this.txt.position.set(0, -75);
        this.addChild(this.txt);

        const back = new NineSlicePlane(Texture.from("top_back.png"), 46, 50, 46, 50);
        back.pivot.set(160, 70);
        back.width = 320;
        back.height = 140;
        this.addChild(back);

        this.lockedTxt = new Text("Locked",{fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 50, fill: 0xAAAAAA, fontWeight: "bold", align: "center"});
        this.lockedTxt.anchor.set(0.5, 0.5);
        this.lockedTxt.position.set(0, 0);
        this.addChild(this.lockedTxt);

        this.hearts = [];
        let xx = -100;
        for (let i = 0; i < 3; i++) {
            const heart = new Sprite(Texture.from(`hero.png`));
            heart.alpha = 0.3;
            heart.anchor.set(0.5, 0.5);
            heart.scale.set(0.8);
            heart.position.x = xx;
            this.addChild(heart);
            this.hearts.push(heart);
            xx += 100;
        }
    }
    public updateData(id: number, stars: number) {
        this.txt.text = `Level ${id+1}`; 
        for (let i = 0; i < 3; i++) {
            this.hearts[i].alpha = stars >= i+1 ? 1 : (stars >= 0 ? 0.3: 0);
        }
        this.lockedTxt.visible = stars === -1;
    }
}