import gsap from "gsap";
import { Container, Sprite, Texture } from "pixi.js";

export class Explosion extends Container{
    private static textures;
    private art: Sprite;
    constructor(play: boolean = true, delay: number = 0) {
        super();
        Explosion.createTextures();
        this.art = Sprite.from(Explosion.textures[0]);
        this.art.anchor.set(0.5,0.5);
        this.addChild(this.art);
        this.scale.set(0, 0);
        if (play) this.animate(delay);
    }
    
    public async animate(delay: number = 0) {
        return new Promise<void>((resolve)=>{
            let frame = 0;
            const p = {a: 0};
            gsap.to(p, {a: 1, delay: delay, duration: delay+0.4, onUpdate: ()=> {
                const nf = Math.floor(p.a * 2);
                if (nf !== frame) {
                    frame = nf;
                    this.art.texture = Explosion.textures[frame];
                }
            }});

            gsap.to(this.scale, {x: 1, y: 1, duration: delay+0.1});
            gsap.to(this.scale, {x: 1.3, y: 1.3, delay: delay+0.2, duration: 0.3});
            gsap.to(this, {alpha: 0, delay:delay+0.2, duration: 0.5, onComplete: ()=> {
                resolve();
            }});
        });
        
    }
    static createTextures() {
        if (!Explosion.textures) {
            Explosion.textures = [Texture.from("game_explosion_0.png"), Texture.from("game_explosion_1.png"), Texture.from("game_explosion_2.png")]
        }
    }
}