import { Container } from "@pixi/display";
import { Sprite } from "@pixi/sprite";
import p2, { Body, Shape } from "p2";
import { CircleBlock } from "../game/states/game/CircleBlock";
import { METER_SIZE, unusedBodies, world } from "./BaseGame";

export enum ECollisionGroups {
    NO = 0,
    WALLS = Math.pow(2,0),
    BALLS = Math.pow(2,1),
    MOVER = Math.pow(2,2),
    OBSTACLES = Math.pow(2,3)
}
export abstract class PhysicsSprite extends Container {
    public body: p2.Body;
    public shape : p2.Shape;
    public type: number;
    protected view: Sprite;
    
    constructor(type: number) {
        super();
        this.type = type;
        this.view = this.createView();
        this.view.anchor.set(0.5, 0.5);
        this.addChild(this.view);
    }
    public createBody(physType: 1|2|4, mass: number = 5) {
        this.view.alpha = 1;
        const pos: [number, number] = [this.position.x / METER_SIZE, this.position.y / METER_SIZE];
        this.shape = this.createShape();
        this.body = new Body({type: physType, position: pos, mass, damping: 0, angularDamping: 0});
        this.body.addShape(this.shape);
        world.addBody(this.body);
        (this.body as any).userData = this;
    }
    public clean(i: boolean = false) {
        this.destroyBody(i);
        this.parent && this.parent.removeChild(this);
    }
    protected destroyBody (immidiately: boolean = false) {
        if (this.body) {
            if (immidiately) {
                world.removeBody(this.body);
            } else {
                unusedBodies.push(this.body);
            }
            this.body = undefined;
        }
    }
    public correctType() {
        return this.type !== 0 && (this.type < 20 || this.type === 250 || this.type === 251 || this.type === CircleBlock.skullType);
    }
    protected abstract createView(): Sprite;
    protected abstract createShape(): Shape;
}